import { useNavigate } from 'react-router-dom';
import styles from "../cssDir/inputAll.module.css";
import axios from "axios";
import { React, useState } from 'react';
import Modal from 'react-modal';
import img from '../imgDir/product.PNG'
import errimg from '../imgDir/err_product.png'
import nextimg from '../imgDir/next.png'
import ReactFileReader from "react-file-reader";
import MyResponsiveLine from './Graph';

let success = 0;        // rpm    
let success2 = 0;       // rpm      
let realSuccess = 0;    //    

let listSum = 0;        // rpm     
let listBeforeSum = 0;  // rpm     
let realSum = 0;        //    

let faile = 0;          // rpm    
let faile2 = 0;         // rpm      
let realFaile = 0;      //    

let faileSum = 0;       // rpm    
let faileSum2 = 0;      // rpm      
let realFaileSum = 0;   //    

let data_w = 0;         // rpm    3.0g   ( )
let data_w2 = 0;        // rpm     3.0g   ( )
let data_w3 = 0;        //    3.0g   ( )

let graphData = [   //   .
    {
        "id": "real",
        "color": "hsl(125, 70%, 50%)",
        "data": []
    },
    {
        "id": "first-predict",
        "color": "hsl(317, 70%, 50%)",
        "data": []
    },
    {
        "id": "recomend-rpm",
        "color": "hsl(187, 70%, 50%)",
        "data": []
    },
];

function InputAll() {
    const BackUrl = 'https://pbl-final-yvbcumjjwq-du.a.run.app';    //     
    const LocalUrl = 'http://127.0.0.1:5000';   //    
    const [data, setData] = useState([{}]);                 //     
    const [data2, setData2] = useState([{}]);               //   
    const [isData, setIsData] = useState(true);             //      . true    
    const [data_count, setCount] = useState(0);             //    
    const [modalOpen, setModalOpen] = useState(false);      //      
    const [modalOpen2, setModalOpen2] = useState(false);    //      
    const [modalOpen3, setModalOpen3] = useState(false);    //      
    const [modalOpen4, setModalOpen4] = useState(false);    //      
    const [modalOpen5, setModalOpen5] = useState(false);    //    

    const promiseSetTimeoutInputCSV = (timeToDelay, count, data_list, isTest) => {
        if(count == 0){
            timeToDelay = 0;
        }
        return new Promise((resolve, reject) => {
            setTimeout(async (i, data_list, isTest) => {  // setTimeout   
                const datas = data_list[i].split(",");
                const pv_scale = Number(datas[9]);  // 9   
                const data = {  // request 
                    'E_scr_pv': datas[1],
                    'c_temp_pv': datas[3],
                    'k_rpm_pv': datas[5],
                    'n_temp_pv': datas[7],
                    's_temp_pv': datas[10],
                };
                const config = { "Content-Type": 'application/json' };
                // if(isTest){
                //     setIsData(isData => {   //  useState      
                //         return false;
                //     });
                // }
                try {
                    if(isTest){
                        setIsData(isData => {   //  useState      
                            return false;
                        });
                    }
                    const response = await axios.post(BackUrl + '/predict', data, config) //    DB 
                    // if(isTest){
                    //     setIsData(isData => {   //  useState      
                    //         return false;
                    //     });
                    // }
                    if(isTest){
                        setIsData(isData => {   //  useState      
                            return true;        //  
                        });
                    }
                    setCount(i + 1);          //    
                    setData(response.data); //  
                    if (response.data.rpm_weight != -1 && !response.data.is_rpm_error) { // rpm    
                        success = success + 1;                          // rpm    
                        listSum = listSum + response.data.rpm_weight;   // rpm    
                        data_w = data_w + 3.0;                          // rpm    
                        if (!response.data.is_error) {    //     
                            success2 = success2 + 1;                                        // rpm      
                            listBeforeSum = listBeforeSum + response.data.predicted_weight; // rpm      
                            data_w2 = data_w2 + 3.0;                                        // rpm      
                        } else { //    
                            faile2 = faile2 + 1;                                    // rpm      
                            faileSum2 = faileSum2 + response.data.predicted_weight; // rpm      
                        }
                    } else if (response.data.rpm_weight == -1 && !response.data.is_error) {    // rpm     

                        success = success + 1;                                          // rpm    
                        success2 = success2 + 1;                                        // rpm      

                        listSum = listSum + response.data.predicted_weight;             // rpm    
                        listBeforeSum = listBeforeSum + response.data.predicted_weight; // rpm      

                        data_w = data_w + 3.0;                                          // rpm    
                        data_w2 = data_w2 + 3.0;                                        // rpm      

                    } else if (response.data.rpm_weight != -1 && response.data.is_rpm_error) { // rpm   
                        faile = faile + 1;                                      // rpm    
                        faileSum = faileSum + response.data.predicted_weight;   // rpm    
                        if (response.data.is_error) { //    
                            faile2 = faile2 + 1;                                    // rpm      
                            faileSum2 = faileSum2 + response.data.predicted_weight; // rpm      
                        } else { //    
                            success2 = success2 + 1;                                        // rpm      
                            listBeforeSum = listBeforeSum + response.data.predicted_weight; // rpm      
                            data_w2 = data_w2 + 3.0;                                        // rpm      
                        }
                    } else if (response.data.rpm_weight == -1 && response.data.is_error) { // rpm    
                        faile = faile + 1;                                      // rpm    
                        faile2 = faile2 + 1;                                    // rpm      
                        faileSum = faileSum + response.data.predicted_weight;   // rpm    
                        faileSum2 = faileSum2 + response.data.predicted_weight; // rpm      
                    }
                    if (isTest) { //     
                        if (Math.abs(pv_scale - 3.0) > 0.1) { //    
                            realFaile += 1;                         //    
                            realFaileSum += pv_scale;               //    
                            setData2({ is_error: true, w: pv_scale })   //   ()
                        }
                        else {   //    
                            realSum += pv_scale;                    //    
                            realSuccess += 1;                       //    
                            data_w3 += 3.0;                         //     
                            setData2({ is_error: false, w: pv_scale })  //   ()
                        }
                    }
                    //    , rpm   , rpm   
                    let predictIndex = 0;
                    if (isTest) {
                        predictIndex = 1;
                        graphData[0].data.push({
                            "x": i + 1,
                            "y": pv_scale
                        });
                    }
                    graphData[predictIndex].data.push({
                        "x": i + 1,
                        "y": response.data.predicted_weight
                    });
                    if (response.data.rpm_weight == -1) { // rpm    rpm    rpm   
                        graphData[predictIndex + 1].data.push({
                            "x": i + 1,
                            "y": response.data.predicted_weight
                        });
                    }
                    else {
                        graphData[predictIndex + 1].data.push({
                            "x": i + 1,
                            "y": response.data.rpm_weight
                        });
                    }
                    // setIsData(isData => {   //  useState      
                    //     return false;
                    // });
                    // setIsData(isData => {   //  useState      
                    //     return true;        //  
                    // });
                    //      
                    if(isTest){
                        openModal3();
                    }else{
                        openModal();
                    }

                    if (i + 1 >= data_list.length) {    //         
                        setTimeout(() => {
                            //       
                            graphData[0].data.sort((n, m) => n.x - m.x);
                            graphData[1].data.sort((n, m) => n.x - m.x);
                            if (isTest) {
                                graphData[2].data.sort((n, m) => n.x - m.x);
                                closeModal3();  //   
                                openModal4();   //   
                            }else{
                                closeModal();  //   
                                openModal2();   //   
                            }
                            
                        }, timeToDelay)
                    }
                    resolve();

                } catch (err) {
                    console.log(err);
                    reject();
                }
            }, timeToDelay, count, data_list, isTest);
        })
    };
    const movePage = useNavigate();
    const testHandleFileChangeEvent = (files) => {
        handleFileChangeFunction(files, true);
    }
    const realHandleFileChangeEvent = (files) => {
        handleFileChangeFunction(files, false);
    }
    const handleFileChangeFunction = (files, isTest) => {
        //  
        listSum = 0;
        faile = 0;
        faile2 = 0;
        faileSum = 0;
        faileSum2 = 0;
        data_w = 0;
        data_w2 = 0;
        data_w3 = 0;
        success = 0;
        success2 = 0;
        listBeforeSum = 0;
        realSum = 0;
        realFaile = 0;
        realSuccess = 0;
        realFaileSum = 0;
        graphData = [
            {
                "id": "first-predict",
                "color": "hsl(317, 70%, 50%)",
                "data": []
            },
            {
                "id": "recomend-rpm",
                "color": "hsl(187, 70%, 50%)",
                "data": []
            },
        ];
        let timeToDelay = 0;
        if (isTest) { //     ,  2
            graphData.unshift({
                "id": "real",
                "color": "hsl(125, 70%, 50%)",
                "data": []
            });
            timeToDelay = 1000;
        }
        const read = new FileReader();  //  
        read.onload = async function (e) {    //      
            let data_list = read.result.split(/(?:\r\n|\r|\n)/g).slice(1);  //      (     )
            if (data_list[data_list.length - 1] <= 1) {  //      
                data_list = data_list.slice(0, -1);
            }
            for (let i = 0; i < data_list.length; i++) {
                await promiseSetTimeoutInputCSV(timeToDelay, i, data_list, isTest);
            }
        };
        read.readAsText(files[0]);  //  (onload )
    }
    
    function goHome() { //   
        movePage('/');
        window.location.reload();
    }

    //    
    const openModal = () => {
        setModalOpen(true);
    }
    const closeModal = () => {
        setModalOpen(false);
    }
    const openModal2 = () => {
        setModalOpen2(true);
    }
    const closeModal2 = () => {
        setModalOpen2(false);
        openModal5();
    }
    const openModal3 = () => {
        setModalOpen3(true);
    }
    const closeModal3 = () => {
        setModalOpen3(false);
    }
    const openModal4 = () => {
        setModalOpen4(true);
    }
    const closeModal4 = () => {
        setModalOpen4(false);
        openModal5();
    }
    const openModal5 = () => {
        setModalOpen5(true);
    }
    const closeModal5 = () => {
        setModalOpen5(false);
    }

    return (
        <div className={styles.App}>
            <div className={styles.input_page}>
                <div className={styles.input_wrap}>
                    <div className={styles.input_box}>
                        <h1 className={styles.input_head}>Manufacturing multiple products</h1>
                        <div className={styles.input_form}>
                            <ReactFileReader handleFiles={testHandleFileChangeEvent} fileTypes={".csv"}>
                                <div className={styles.input_btn_wrap2}>
                                    <button className={styles.test_btn_under}>Upload CSV to Test</button>
                                    <button className={styles.test_btn}><span>Upload CSV to Test</span></button>
                                </div>
                            </ReactFileReader>
                            <ReactFileReader handleFiles={realHandleFileChangeEvent} fileTypes={".csv"}>
                                <div className={styles.input_btn_wrap2}>
                                    <button className={styles.input_btn_under}>Upload CSV to Manufacture</button>
                                    <button className={styles.input_btn}><span>Upload CSV to Manufacture</span></button>
                                </div>
                            </ReactFileReader>
                            <div className={styles.input_btn_wrap2}>
                                <button type="button" className={styles.home_btn_under} onClick={goHome}>Go Back Home</button>
                                <button type="button" className={styles.home_btn} onClick={goHome}><span>Go Back Home</span></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Modal
                isOpen={modalOpen}
                onRequestClose={closeModal}
                ariaHideApp={false}
                style={{
                    content: {
                        margin: '0 auto',
                        height: '80vh',
                        backgroundColor: 'whitesmoke',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    },
                    overlay: {
                        backgroundColor: 'rgba(0, 0, 0, 0.5)',
                        zIndex: 30,
                        textAlign: 'center',
                    },
                }}>
                {isData ?
                    <div className={styles.result_wrap}>
                        <div className={styles.result_title}>
                            <h2>{data_count} </h2>
                            <div>  : {success}</div>
                            <div>   : {Math.round(listSum * 1000) / 1000}g(loss : {(Math.round((listSum - data_w) * 1000000) / 1000000)}g)</div>
                            <div>  : {faile}(  {faileSum}g )</div>
                        </div>
                        <div className={styles.result_box}>
                            {
                                (typeof data.predicted_weight === 'undefined') ?
                                    <p>loding...</p>
                                    : data.recommended_rpm !== -1 ?
                                        <div className={styles.result_boxR}>
                                            <div className={styles.result_div}>
                                                {data.is_error ?
                                                    <img src={errimg} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                                    : <img src={img} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                                }
                                                <p> - </p>
                                                <h2>   : {data.predicted_weight}g</h2>
                                                <p> - </p>
                                            </div>
                                            <img src={nextimg} width={200} height={200} className={styles.next_img} alt={'  .'} />
                                            <div className={styles.result_div}>
                                                {data.is_rpm_error ?
                                                    <img src={errimg} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                                    : <img src={img} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                                }
                                                <div>
                                                    <p>  RPM : {data.recommended_rpm}</p>
                                                    <h2>RPM   <br></br>  : {data.rpm_weight}g</h2>
                                                    <h3>  : {Math.round(Math.abs(data.predicted_weight - data.rpm_weight) * 1000) / 1000}g</h3>
                                                </div>
                                            </div>
                                        </div>
                                        :
                                        <div className={styles.result_boxN}>
                                            <div className={styles.result_div}>
                                                {data.is_error ?
                                                    <img src={errimg} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                                    : <img src={img} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                                }
                                                <p> - </p>
                                                <h2>  : {data.predicted_weight}g</h2>
                                                <p> - </p>
                                            </div>
                                            <></>
                                            <></>
                                        </div>
                            }
                        </div>
                    </div>
                    : <></>}
            </Modal>
            <Modal
                isOpen={modalOpen3}
                onRequestClose={closeModal3}
                ariaHideApp={false}
                style={{
                    content: {
                        margin: '0 auto',
                        height: '80vh',
                        backgroundColor: 'whitesmoke',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    },
                    overlay: {
                        backgroundColor: 'rgba(0, 0, 0, 0.5)',
                        zIndex: 30,
                        textAlign: 'center',
                    },
                }}>
                {isData ?
                    <div className={styles.result_wrap}>
                        <div className={styles.result_title}>
                            <h2>{data_count} </h2>
                            <div>  : {success}</div>
                            <div>   : {Math.round(listSum * 1000) / 1000}g(loss : {(Math.round((listSum - data_w) * 1000000) / 1000000)}g)</div>
                            <div>  : {faile}(  {faileSum}g )</div>
                        </div>
                        <div className={styles.result_box}>
                            {(typeof data.predicted_weight === 'undefined') ? (
                                <p>loding...</p>
                            ) : data.recommended_rpm !== -1 ?
                                <div className={styles.result_boxR}>
                                    <div className={styles.result_div}>
                                        {Math.abs(data2.w - 3.0) > 0.1 ?
                                            <img src={errimg} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                            : <img src={img} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                        }
                                        <p> - </p>
                                        <h2>  : {data2.w}g</h2>
                                        <h2>   :<br></br>{data.predicted_weight}g( {Math.round((data.predicted_weight - data2.w) * 1000) / 1000}g )</h2>
                                    </div>
                                    <img src={nextimg} width={200} height={200} className={styles.next_img} alt={'  .'} />
                                    <div className={styles.result_div}>
                                        {data.is_rpm_error ?
                                            <img src={errimg} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                            : <img src={img} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                        }
                                        <div>
                                            <p>  RPM : {data.recommended_rpm}</p>
                                            <h2>RPM   <br></br>  : {data.rpm_weight}g</h2>
                                            <h3>  : {Math.round(Math.abs(data2.w - data.rpm_weight) * 1000) / 1000}g</h3>
                                        </div>
                                    </div>
                                </div>
                                :
                                <div className={styles.result_boxR}>
                                    <div className={styles.result_div}>
                                        {Math.abs(data2.w - 3.0) > 0.1 ?
                                            <img src={errimg} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                            : <img src={img} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                        }
                                        <p> - </p>
                                        <h2>  : {data2.w}g</h2>
                                        <h2>   :<br></br>{data.predicted_weight}g( {Math.round((data.predicted_weight - data2.w) * 1000) / 1000}g )</h2>
                                    </div>
                                    <img src={nextimg} width={200} height={200} className={styles.next_img} alt={'  .'} />
                                    <div className={styles.result_div}>
                                        {data.is_error ?
                                            <img src={errimg} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                            : <img src={img} width={200} height={200} className={styles.product_img} alt={'  .'} />
                                        }
                                        <div>
                                            <p> RPM </p>
                                            <h2>  : {data.predicted_weight}g</h2>
                                            <h3>  : {Math.round(Math.abs(data2.w - data.predicted_weight) * 1000) / 1000}g</h3>
                                        </div>
                                    </div>
                                </div>
                            }
                        </div>
                    </div>
                    : <></>}
            </Modal>
            <Modal
                isOpen={modalOpen2}
                onRequestClose={closeModal2}
                ariaHideApp={false}
                style={{
                    content: {
                        margin: '0 auto',
                        height: '80vh',
                        backgroundColor: 'whitesmoke',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    },
                    overlay: {
                        backgroundColor: 'rgba(0, 0, 0, 0.5)',
                        zIndex: 30,
                        textAlign: 'center',
                    },
                }}>
                <div className={styles.result_wrap}>
                    <div className={styles.result_title}><h2>{success + faile}   </h2></div>
                    <div className={styles.result_boxR}>
                        <div className={styles.result_div}>
                            <div><h2>RPM     :<br></br>{success2}</h2></div>
                            <div><h2>RPM       :<br></br>{Math.round(listBeforeSum * 1000) / 1000}g</h2></div>
                            <div><h2>RPM    loss :<br></br>{(Math.round((listBeforeSum - data_w2) * 1000000) / 1000000)}g</h2></div>
                            <div><h3>RPM     :<br></br>{faile2}</h3></div>
                            <div><h3>RPM      loss :<br></br>{faileSum2}g</h3></div>
                        </div>
                        <img src={nextimg} width={200} height={200} className={styles.next_img} alt={'  .'} />
                        <div className={styles.result_div}>
                            <div><h2>  :<br></br>{success}{success > success2 ? <>(+{success - success2})</> : <></>}</h2></div>
                            <div><h2>    :<br></br>{Math.round(listSum * 1000) / 1000}g{Math.round(listSum * 1000) / 1000 < Math.round(listBeforeSum * 1000) / 1000 ?
                                <>({Math.round((listSum - listBeforeSum) * 1000) / 1000}g)</> :
                                <></>}</h2></div>
                            <div><h2> loss :<br></br>{(Math.round((listSum - data_w) * 1000000) / 1000000)}g
                                {(Math.round((listSum - data_w) * 1000000) / 1000000) < (Math.round((listBeforeSum - data_w2) * 1000000) / 1000000) ?
                                    <>({(Math.round((listSum - data_w - listBeforeSum + data_w2) * 1000000) / 1000000)}g)</> :
                                    <></>}</h2></div>
                            <div><h3>  :<br></br>{faile}{faile < faile2 ? <>({faile - faile2})</> : <></>}</h3></div>
                            <div><h3>   loss :<br></br>{faileSum}g{faileSum < faileSum2 ? <>({faileSum - faileSum2})</> : <></>}</h3></div>
                        </div>

                    </div>
                    <div className={styles.input_btn_wrap_div}>
                        <div className={styles.input_btn_wrap}>
                            <button className={styles.cancel_btn_under} onClick={closeModal2}>OK</button>
                            <button className={styles.cancel_btn} onClick={closeModal2}><span>OK</span></button>
                        </div>
                    </div>
                </div>
            </Modal>
            <Modal
                isOpen={modalOpen4}
                onRequestClose={closeModal4}
                ariaHideApp={false}
                style={{
                    content: {
                        margin: '0 auto',
                        height: '80vh',
                        backgroundColor: 'whitesmoke',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    },
                    overlay: {
                        backgroundColor: 'rgba(0, 0, 0, 0.5)',
                        zIndex: 30,
                        textAlign: 'center',
                    },
                }}>
                <div className={styles.result_wrap2}>
                    <div className={styles.result_title2}><h2>{success + faile}   </h2></div>
                    <div className={styles.result_box3}>
                        <div className={styles.result_div2}>
                            <div><h2>  :<br></br>{realSuccess}</h2></div>
                            <div><h2>    :<br></br>{Math.round(realSum * 1000) / 1000}g</h2></div>
                            <div><h2> loss :<br></br>{(Math.round((realSum - data_w3) * 1000000) / 1000000)}g</h2></div>
                            <div><h3>  :<br></br>{realFaile}</h3></div>
                            <div><h3>   loss :<br></br>{realFaileSum}g</h3></div>
                        </div>
                        <img src={nextimg} width={200} height={200} className={styles.next_img2} alt={'  .'} />
                        <div className={styles.result_div2}>
                            <div><h2>RPM     :<br></br>{success2}</h2></div>
                            <div><h2>RPM       :<br></br>{Math.round(listBeforeSum * 1000) / 1000}g</h2></div>
                            <div><h2>RPM    loss :<br></br>{(Math.round((listBeforeSum - data_w2) * 1000000) / 1000000)}g</h2></div>
                            <div><h3>RPM     :<br></br>{faile2}</h3></div>
                            <div><h3>RPM      loss :<br></br>{faileSum2}g</h3></div>
                        </div>
                        <img src={nextimg} width={200} height={200} className={styles.next_img2} alt={'  .'} />
                        <div className={styles.result_div2}>
                            <div><h2>  :<br></br>{success}{success > realSuccess ? <>(+{success - realSuccess})</> : <></>}</h2></div>
                            <div><h2>    :<br></br>{Math.round(listSum * 1000) / 1000}g{Math.round(listSum * 1000) / 1000 < Math.round(realSum * 1000) / 1000 ?
                                <>({Math.round((listSum - realSum) * 1000) / 1000}g)</> :
                                <></>}</h2></div>
                            <div><h2> loss :<br></br>{(Math.round((listSum - data_w) * 1000000) / 1000000)}g
                                {(Math.round((listSum - data_w) * 1000000) / 1000000) < (Math.round((realSum - data_w3) * 1000000) / 1000000) ?
                                    <>({(Math.round((listSum - data_w - realSum + data_w3) * 1000000) / 1000000)}g)</> :
                                    <></>}</h2></div>
                            <div><h3>  :<br></br>{faile}{faile < realFaile ? <>({faile - realFaile})</> : <></>}</h3></div>
                            <div><h3>   loss :<br></br>{faileSum}g{faileSum < realFaileSum ? <>({faileSum - realFaileSum})</> : <></>}</h3></div>
                        </div>

                    </div>
                    <div className={styles.input_btn_wrap_div3}>
                        <div className={styles.input_btn_wrap3}>
                            <button className={styles.cancel_btn_under} onClick={closeModal4}>OK</button>
                            <button className={styles.cancel_btn} onClick={closeModal4}><span>OK</span></button>
                        </div>
                    </div>
                </div>
            </Modal>
            <Modal
                isOpen={modalOpen5}
                onRequestClose={closeModal5}
                ariaHideApp={false}
                style={{
                    content: {
                        margin: '0 auto',
                        height: '80vh',
                        backgroundColor: 'whitesmoke',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    },
                    overlay: {
                        backgroundColor: 'rgba(0, 0, 0, 0.5)',
                        zIndex: 30,
                        textAlign: 'center',
                    },
                }}>
                <div className={styles.result_wrap}>
                    <MyResponsiveLine data={graphData}></MyResponsiveLine>
                    <div className={styles.input_btn_wrap_div2}>
                        <div className={styles.input_btn_wrap2}>
                            <button className={styles.cancel_btn_under} onClick={closeModal5}>OK</button>
                            <button className={styles.cancel_btn} onClick={closeModal5}><span>OK</span></button>
                        </div>
                    </div>
                </div>
            </Modal>
        </div>
    );
}
export default InputAll;